    <select class="s0">
      <option >대분류 선택</option>
      <option value="1">설치</option>
      <option value="2">AS</option>
      <option value="3">PM</option>
      <option value="4">지원</option>
      <option value="5">원격지원</option>
      <option value="6">US</option>
      <option value="7">파트교체</option>
