<script src="https://code.jquery.com/jquery-3.5.1.min.js"></script>
<script>
// 대분류 소분류 선택
import $ from "jquery";
$(document).ready(function(){
  var install=['SIGMA || 설치','110HQ 설치','210 설치', 'DR 일반촬영실 설치','DR 포터블 설치', 'DR로밍시스템 설치', '기존장비 이전 설치','중고장비 설치', 'US 설치'];
  var AS=['CR장비 긴급 고장', 'DR장비 긴급 고장','PACS 관련 긴급 고장','납품 콘솔 긴급 고장', 'X-Ray 관련 긴급 고장', 'US장비 긴급 고장', 'Sigma롤러 청소','콘솔 재설치'];
  var PM=['정기 예방점검 진행', '이미지 예방점검 진행', 'X-ray 예방점검 진행','이미지 보정 및 점검','패널보정 및 점검','코드맵핑 및 점검','패널보정 및 이미지 보정']
  var support=['오픈지원','사용자 교육', '맘모 정도관리 지원', '데모지원', '설치 후 콘솔셋팅', '이미지보정 방문요청']
  var remote=['(원격)PACS 뷰어설치','(원격) 모달리티 연동지원', '(원격)콘솔서비스 지원','(원격)서버장애처리','원격판독','(원격)PACS 사용자 교육', '(원격)CS7 사용자 교육','(원격) OCS 연동', '(원격)PACS 장애처리', '(원격)이미지 보정', '(원격)PPP 서비스', '(원격)게이트웨이 서비스', '(원격)콘솔 장애처리', '(원격)이미지파일럿 장애처리']
  var US=['임상지원','학회지원']
  var part=['Halogen Lamp 교체', 'FAN 교체', 'GIUDE 교체', 'Tumbler 교체', 'MDU보드 교체', 'LPB, LLB 교체','Optical Unit 교체', 'Sigma Eraser Unit 교체', 'Sigma MainBoard 교체', 'MCB 보드 교체', 'CST BackPlate 교체', 'SW Power Board 교체', '필터교체(드라이프로)','필터교체(CR)']
  var MIM=['MIM 설치', 'MIM -PW', 'MIM A/S','MIM DEMO','MIM 학회','MIM 교육',]
  var Xray=['방향키 AS', '콜리메이터 AS', '핸드스위치 AS', '콘솔멤브레인 AS', '제너레이터 AS', 'Tube AS']

  $('.s0').change(function(){
    var sel= $(this).val();
    if(sel==1){
      $('.op').remove();
      $.each(install, function(i, item){
        $('.s1').append('<option class="op">'+item+'</option>');
      });
    }
    else if(sel==2){
       $('.op').remove();
        $.each(AS, function(i, item){
             $('.s1').append('<option class="op">'+item+'</option>');
           
        });
    }
    else if(sel==3){
       $('.op').remove();
        $.each(PM, function(i, item){
             $('.s1').append('<option class="op">'+item+'</option>');
           
        });
    }
    else if(sel==4){
       $('.op').remove();
        $.each(support, function(i, item){
             $('.s1').append('<option class="op">'+item+'</option>');
           
        });
    }
    else if(sel==5){
       $('.op').remove();
        $.each(remote, function(i, item){
             $('.s1').append('<option class="op">'+item+'</option>');
           
        });
    }
    else if(sel==6){
       $('.op').remove();
        $.each(US, function(i, item){
             $('.s1').append('<option class="op">'+item+'</option>');
           
        });
    }
    else if(sel==7){
       $('.op').remove();
        $.each(part, function(i, item){
             $('.s1').append('<option class="op">'+item+'</option>');
           
        });
    }
    else if(sel==8){
       $('.op').remove();
        $.each(MIM, function(i, item){
             $('.s1').append('<option class="op">'+item+'</option>');
           
        });
    }
   
    else{
       $('.op').remove();
        $.each(Xray, function(i, item){
             $('.s1').append('<option class="op">'+item+'</option>');
           
        });
    }
  })
})
require('date-utils');
import Vue from "vue";
import axios from 'axios';
import imageCompressor from 'vue-image-compressor'

 const multer = require('multer');
export default {

  


  components: { imageCompressor },

  data: () => ({
    selectOne: '', //대분류 값
    files: [],
    date: "",
    name: "",
    contents: "",
     form: {
      name: '서비스 리포트 이미지 업로드'
    },
     img: "",
    scale: 100,
    quality: 50,
    originalSize: true,
    original: {},
    searchTerm: '',

  }),

  methods: {

   
   



    //이미지 저장  
    upload () {
          var para = document.location.href.split("?")

      const fd = new FormData()
      fd.append('name', this.form.name)
      fd.append('bin', document.getElementById('bin').files[0])

      axios.post('http://192.1.1.30:3000/ind/image', fd)
        .then((req, res) => {
          
         alert('이미지 저장 성공')
        console.log(document.getElementById('bin').files[0])
        console.log(para)
        })
        .catch(e => {
         alert('실패')
        })
    },


    //텍스트 INSERT넣기 서비스 리포트
    textUpload:function() {  
          //파일명 guidname 추가
          var newDate=new Date();
          var time=newDate.toFormat('YYYYMMDD');        
          var date=time;
         console.log(date)

          var params = new URLSearchParams();
          params.append('date', date);
          params.append('searchText', this.searchText);
          params.append('searchName', this.searchName);
          params.append('searchRegdate', this.searchRegdate);
          // params.append('selectOne', this.selectOne);

             
          axios
            .post('http://192.1.1.30:3000/ind/serviceReportSend', params)
            .then(res => {
            console.log(res.data)
              // //첫번째 배열 가져오기
              // const InfoArray = Object.values(res.data[0])
              this.items=res.data      
              console.log(res)   
              console.log('성공')
              alert('내용 보내기 성공')
            })
            .catch(function (err) {
              console.log(err);
            })
    },


    
  getFiles(obj){
      this.img = obj.compressed.blob
      this.original = obj.original
      this.compressed = obj.compressed
  },
  
  }
}
</script>

